import {
    Component,
	ViewContainerRef,
	ViewChild,
	ComponentFactoryResolver,
	Output,
	EventEmitter
  } from '@angular/core'

  import { CodeTerminalBuildComponent } from './home.codeterminal.build.component'
@Component({
  template: require('./home.codeterminal.html')
})
export class CodeTerminalComponent {
	@ViewChild('codeterminal', {read: ViewContainerRef}) codeTerminalDivContainerRef;
	@Output()
	uploaded:EventEmitter<string> = new EventEmitter();
  
  public isViewInitialized = false;
 
  constructor(public componentFactoryResolver:ComponentFactoryResolver) {}


  updateComponent() {
	this.uploaded.emit('complete');
  }

  ngOnChanges() {
    this.updateComponent();
  }

  ngAfterViewInit() {
    this.isViewInitialized = true;
    this.updateComponent();  
  }

  ngOnDestroy() {
  
  }
}


