import {
    Component,
	ViewContainerRef,
	ViewChild,
	ComponentFactoryResolver
  } from '@angular/core'

  import {DomSanitizer} from '@angular/platform-browser';
  
@Component({
  
  template: require('./home.codeterminal.build.html')
})
export class CodeTerminalBuildComponent {
	@ViewChild('codeterminal', {read: ViewContainerRef}) codeTerminalDivContainerRef;
	
  public isViewInitialized = false;
  public resultOfBuild = "";
  public resultOfBuildHtml : any;
  constructor(public componentFactoryResolver:ComponentFactoryResolver, public sanitizer: DomSanitizer) {}

  public buildCodeTerminal(){
  const factory = this.componentFactoryResolver.resolveComponentFactory(CodeTerminalBuildComponent);
        const ref = this.codeTerminalDivContainerRef.createComponent(factory);
  }
  
  updateComponent() {
	
  }

  ngOnChanges() {
    this.updateComponent();
  }

  ngOnInit() {
    this.isViewInitialized = true;
    this.updateComponent();
	setTimeout(() => {this.resultOfBuild += "<p>||=== Build: Debug in OnlinePortfolio (compiler: GNU GCC Compiler) ===|\n";
					  this.resultOfBuildHtml = this.sanitizer.bypassSecurityTrustHtml(this.resultOfBuild);}, 1000 );
	setTimeout(() => {this.resultOfBuild += "||=== Build finished: 0 error(s), 0 warning(s) (0 minute(s), 2 second(s)) ===|\n"
										 +	"<a href=\"test\">Execute Portfolio.exe</a> ?</p>"; console.log(this.resultOfBuild);
										 this.resultOfBuildHtml = this.sanitizer.bypassSecurityTrustHtml(this.resultOfBuild);}, 4000 );
					
  }

  ngOnDestroy() {
  
  }
}


