import {
  Component,
  OnInit,
  ComponentFactoryResolver,
  ViewChild,
  ViewContainerRef
} from '@angular/core';

import { AppState } from '../app.service';
import { Title } from './title';
import { XLargeDirective } from './x-large';
import './../../prettify.js';

import { CodeTerminalComponent} from './home.codeterminal.component';
import { CodeTerminalBuildComponent} from './home.codeterminal.build.component';
import { CodeTerminalRunComponent} from './home.codeterminal.run.component';

import { ChangeDetectorRef } from "@angular/core";

declare var PR: any; 

@Component({
  // The selector is what angular internally uses
  // for `document.querySelectorAll(selector)` in our index.html
  // where, in this case, selector is the string 'home'
  selector: 'home',  // <home></home>
  // We need to tell Angular's Dependency Injection which providers are in our app.
  providers: [
    Title
  ],
  // Our list of styles in our component. We may add more to compose many styles together
  styles: [ require('./home.component.css') ],
  // Every Angular template is first compiled by the browser before Angular runs it's compiler
  template: require('./home.component.html')
})
export class HomeComponent implements OnInit {
  @ViewChild('codeterminal', {read: ViewContainerRef}) codeterminal;
  
  
  // Set our default values
  public localState = { value: '' };
  public skillsTooltipContents;
  public codeterminalref : any;
  public buildButtonText = "BUILD";
  public buildToolTipText = "Builds the application";
  public codeTerminalStatus = true;
  // TypeScript public modifiers
  constructor(
    public appState: AppState,
    public title: Title,
	public componentFactoryResolver : ComponentFactoryResolver,
	public ref: ChangeDetectorRef
  ) {
	this.skillsTooltipContents = "<div class=\"hover\">NicolasYazmanSkills<div class=\"tooltip\"><div class=\"skills\"><h2>My Skills</h2><ul><li><span class=\"html5\"></span><em>HTML 5</em></li><li><span class=\"css3\"></span><em>CSS 3</em></li><li><span class=\"jquery\"></span><em>jQuery</em></li><li><span class=\"photoshop\"></span><em>Photoshop</em></li></ul></div></div></div>";
  }

  public buildCodeTerminal(){
  const factory = this.componentFactoryResolver.resolveComponentFactory(CodeTerminalComponent);
     this.codeterminalref = this.codeterminal.createComponent(factory);	
  }
  
  public ngOnInit() {
    console.log('hello `Home` component');
	
	this.buildCodeTerminal();
    // this.title.getData().subscribe(data => this.data = data);
  }

  public ngAfterViewChecked()
  {
	PR.prettyPrint();
  }
  
  public createBuildCodeTerminal()
  {
		if (this.codeTerminalStatus)
		{
			this.buildButtonText = "RETURN TO CODE";
			this.buildToolTipText = "Get back to the code!";
			this.codeterminal.clear();
			const factory = this.componentFactoryResolver.resolveComponentFactory(CodeTerminalBuildComponent);
			const ref = this.codeterminal.createComponent(factory);
			this.ref.detectChanges();
		}
		else
		{
			this.buildButtonText = "BUILD";
			this.buildToolTipText = "Builds the application";
			this.codeterminal.clear();
			const factory = this.componentFactoryResolver.resolveComponentFactory(CodeTerminalComponent);
			const ref = this.codeterminal.createComponent(factory);
			this.ref.detectChanges();
		}
		this.codeTerminalStatus = !this.codeTerminalStatus;
		console.log(this.buildToolTipText);
  }
  
  public createRunCodeTerminal()
  {
		this.codeterminal.clear();
		const factory = this.componentFactoryResolver.resolveComponentFactory(CodeTerminalRunComponent);
		const ref = this.codeterminal.createComponent(factory);	
  }
  
  public submitState(value: string) {
    console.log('submitState', value);
    this.appState.set('value', value);
    this.localState.value = '';
  }
}



