import {
  Component,
  OnInit
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Params } from '@angular/router';
@Component({
  selector: 'search-page-body',
  styles: [`
  `],
  template: `
    <h1>Search</h1>
    <div>
	   <ul class="collection">
	     <li class="collection-item avatar" *ngFor="let searchRes of this.localState; let i = index">
			 <i class="material-icons circle">folder</i>
			 <span class="title">{{searchRes}}</span>
		 <p>Search result {{i}}</p>		 
		 </li>
	   </ul>
    </div>
    
  `
})
export class SearchPageComponent implements OnInit {

  public localState: any;
  constructor(
    public route: ActivatedRoute
  ) {}

  public ngOnInit() {
    
	/*this.route
      .data
      .subscribe((data: any) => {
        // your resolved data from route
        this.localState = data.yourData;
		console.log(data);
		console.log(this.localState);
      });
	*/
	this.route.queryParams.subscribe((params: Params) => {
	 
	   this.localState = Object.keys(params).map(function(key){ return [params[key]];});
	   console.log(this.localState);
	});
    // static data that is bundled
    // var mockData = require('assets/mock-data/mock-data.json');
    // console.log('mockData', mockData);
    // if you're working with mock data you can also use http.get('assets/mock-data/mock-data.json')
    //this.asyncDataWithWebpack();
  }
  private asyncDataWithWebpack() {
    // you can also async load mock data with 'es6-promise-loader'
    // you would do this if you don't want the mock-data bundled
    // remember that 'es6-promise-loader' is a promise
    setTimeout(() => {

      System.import('../../assets/mock-data/mock-data.json')
        .then((json) => {
          console.log('async mockData', json);
          this.localState = json;
        });

    });
  }

}



